<template>
	<div>
		<h3>Editar contato</h3>
		<formulario :contato="contato" @cep="cep" tipo="editar" @submit="salvar"></formulario>
	</div>
</template>

<script>
	import Formulario from '@/components/Formulario'

	export default {
		name:'Editar',
		data(){
			return {
				contato:{}
			}
		},
		created(){
			this.$http.get('contatos/'+this.$route.params.id).then(response=>{
				this.contato = response.data.contato
			})
		},
		methods:{
			salvar(contato){
				delete contato.email
				this.$http.patch('contatos/'+this.$route.params.id,contato).then(response=>{
					this.$router.push({name:'home'})
				})
				
			}
		},
		components:{
			Formulario
		}
	}
</script>
