<template>
	<div>
		<h3>Cadastrar contato</h3>
		<formulario @submit="salvar"></formulario>
	</div>
</template>

<script>
	import Formulario from '@/components/Formulario'
	export default {
		name:'Cadastrar',
		methods:{
			salvar(contato){
				this.$http.post('contatos',contato)
				.then(response=>{
					this.$router.push({name:'home'})
				})
				.catch(e=>{
					if(e.response.status == 409){
						alert('email ja cadastrado')
					}else{
						alert('error no servidor, entre em contato com o administrador')
					}

				})
				
			}
		},
		components:{
			Formulario
		}
	}
</script>
